
.v-enter-active, .v-leave-active {
  -webkit-transition-property: opacity;
  transition-property: opacity;
  -webkit-transition-duration: .5s;
          transition-duration: .5s;
}
.v-enter-active {
  -webkit-transition-delay: .5s;
          transition-delay: .5s;
}
.v-enter, .v-leave-active {
  opacity: 0
}




